:root {
  font-size: 62.5%; }

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
  box-sizing: border-box; }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.header {
  height: 158px;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: flex-end;
  justify-content: end; }

.image-aweprocar {
  height: 63px;
  width: 250px; }

/* TABLET VERSION */
@media all and (min-width: 768px) {
  .header {
    height: 455px;
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: flex-end;
    justify-content: end; }
  .image-aweprocar {
    height: 105px;
    width: 419px; } }

/*DESKTOP VERSION*/
@media all and (min-width: 1200px) {
  .header {
    height: 111px;
    background-color: #fff;
    flex-direction: row;
    justify-content: flex-start; }
  .image-aweprocar {
    height: 50px;
    width: 201px;
    margin-top: 31px;
    margin-bottom: 30px;
    margin-left: 40px; } }

.fill__legend {
  display: flex;
  border-top: 0.1rem #d8d8d8 solid;
  margin: 0 2rem 2.5rem 2rem;
  justify-content: space-between; }
  .fill__legend__div {
    display: flex; }
    .fill__legend__div__icon {
      color: #e17334;
      font-size: 1.8rem;
      margin: 1rem 1rem 0 0; }
    .fill__legend__div__title {
      font-size: 1.8rem;
      text-transform: uppercase;
      color: #54585a;
      font-family: "Open Sans", sans-serif;
      margin-top: 1rem;
      font-weight: 600; }
  .fill__legend__arrow {
    margin-top: 1rem;
    font-size: 1.8rem;
    color: #54585a; }

.fill__div {
  display: flex;
  flex-direction: column;
  margin: 0 2rem 0 2rem; }
  .fill__div__title {
    color: #114e4e;
    font-size: 1.4rem;
    margin: 2rem 0 0.7rem 0;
    font-family: "Open Sans", sans-serif; }
  .fill__div__input {
    border: 1px solid #a2deaf;
    height: 3rem; }
  .fill__div__section {
    display: flex; }
    .fill__div__section__button {
      color: white;
      background-color: #e17334;
      height: 3rem;
      width: 14rem;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      font-family: "Open Sans", sans-serif;
      border-radius: 3px;
      margin-right: 2rem;
      box-shadow: 0 0 4px 0 black; }
    .fill__div__section__rectangle {
      border: 1px solid #a2deaf;
      height: 3rem;
      width: 3rem; }

::placeholder {
  color: #c5c5c5;
  font-size: 1.2em; }

.share__legend {
