  .share__legend__div {
    display: flex; }
    .share__legend__div__icon {
      color: #e17334;
      font-size: 1.8rem;
      margin: 1rem 1rem 0 0; }
    .share__legend__div__title {
      font-size: 1.8rem;
      text-transform: uppercase;
      color: #54585a;
      font-family: "Open Sans", sans-serif;
